'use client';

import { useWallet } from '../app/context/WalletContext';

export function WalletValue() {
  const { account, walletBalance, isConnecting, error, connectWallet } = useWallet();

  if (error) {
    return (
      <div className="rounded-2xl bg-[#060709] p-6">
        <div className="text-red-500 mb-4">{error}</div>
        <button
          onClick={connectWallet}
          className="px-4 py-2 bg-blue-500 rounded-lg hover:bg-blue-600 transition-colors"
        >
          Try Again
        </button>
      </div>
    );
  }

  if (!account) {
    return (
      <div className="rounded-2xl bg-[#060709] p-6">
        <button
          onClick={connectWallet}
          disabled={isConnecting}
          className="px-4 py-2 bg-blue-500 rounded-lg hover:bg-blue-600 transition-colors disabled:opacity-50"
        >
          {isConnecting ? 'Connecting...' : 'Connect Wallet'}
        </button>
      </div>
    );
  }

  return (
    <div className="rounded-2xl bg-[#060709] p-6">
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-lg font-medium">Wallet Value</h3>
        <button className="text-gray-400 hover:text-gray-300">
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>

      <div className="text-3xl font-bold mb-2">
        {walletBalance.toLocaleString('en-US', { minimumFractionDigits: 2 })} ETH
      </div>

      <div className="flex items-center gap-2 text-sm text-blue-400">
        <span>WALLET CONNECTED</span>
        <div className="flex -space-x-2">
          <div className="w-6 h-6 rounded-full bg-gray-700 border-2 border-[#14151f]" />
        </div>
      </div>
    </div>
  );
}
