import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { ethers, BrowserProvider } from 'ethers';

interface WalletContextType {
  account: string | null;
  walletBalance: number;
  isConnecting: boolean;
  error: string | null;
  connectWallet: () => Promise<void>;
  disconnectWallet: () => void;
  updateProfile: (name: string, avatarUrl: string) => Promise<void>;
  profile: {
    name: string;
    avatarUrl: string;
    solidPoints: number;
    multiplier: number;
  };
}

const WalletContext = createContext<WalletContextType | undefined>(undefined);

export function WalletProvider({ children }: { children: ReactNode }) {
  const [account, setAccount] = useState<string | null>(null);
  const [walletBalance, setWalletBalance] = useState<number>(0);
  const [isConnecting, setIsConnecting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [profile, setProfile] = useState({
    name: '',
    avatarUrl: 'https://avatars.githubusercontent.com/u/default',
    solidPoints: 0,
    multiplier: 1,
  });

  const connectWallet = async () => {
    if (typeof window === 'undefined' || !window.ethereum) {
      setError('MetaMask not detected!');
      return;
    }

    setIsConnecting(true);
    setError(null);

    try {
      const provider = new BrowserProvider(window.ethereum);
      const accounts = await provider.send('eth_requestAccounts', []);
      const account = accounts[0];
      setAccount(account);

      const balance = await provider.getBalance(account);
      const balanceInEth = parseFloat(ethers.formatEther(balance));
      setWalletBalance(balanceInEth);

      // Load profile from localStorage or set default
      const savedProfile = localStorage.getItem(`profile-${account}`);
      if (savedProfile) {
        setProfile(JSON.parse(savedProfile));
      } else {
        setProfile({
          name: `User ${account.slice(0, 6)}`,
          avatarUrl: 'https://avatars.githubusercontent.com/u/default',
          solidPoints: 0,
          multiplier: 1,
        });
      }
    } catch (error) {
      setError('Failed to connect wallet');
      console.error('Error connecting to MetaMask:', error);
    } finally {
      setIsConnecting(false);
    }
  };

  const disconnectWallet = () => {
    setAccount(null);
    setWalletBalance(0);
    setProfile({
      name: '',
      avatarUrl: 'https://avatars.githubusercontent.com/u/default',
      solidPoints: 0,
      multiplier: 1,
    });
  };

  const updateProfile = async (name: string, avatarUrl: string) => {
    if (!account) return;

    const updatedProfile = {
      ...profile,
      name,
      avatarUrl,
    };

    setProfile(updatedProfile);
    localStorage.setItem(`profile-${account}`, JSON.stringify(updatedProfile));
  };

  // Listen for account changes
  useEffect(() => {
    if (typeof window !== 'undefined' && window.ethereum) {
      window.ethereum.on('accountsChanged', (accounts: string[]) => {
        if (accounts.length === 0) {
          disconnectWallet();
        } else {
          connectWallet();
        }
      });
    }

    return () => {
      if (window.ethereum) {
        window.ethereum.removeAllListeners('accountsChanged');
      }
    };
  }, []);

  return (
    <WalletContext.Provider
      value={{
        account,
        walletBalance,
        isConnecting,
        error,
        connectWallet,
        disconnectWallet,
        updateProfile,
        profile,
      }}
    >
      {children}
    </WalletContext.Provider>
  );
}

export function useWallet() {
  const context = useContext(WalletContext);
  if (context === undefined) {
    throw new Error('useWallet must be used within a WalletProvider');
  }
  return context;
} 